<template>
  <div>
    <h1>결제 페이지</h1>
    <!-- <p>파라미터: {{ $route.query }}</p> -->
    <!-- 파라미터를 이용한 뷰 내용 출력 -->
  </div>
  <b-button
    @click="PaymentRequest"
    class="w-100 btn btn-primary btn-lg"
    variant="primary"
    >결제하기</b-button
  >
</template>

<script>
import axios from "axios";


export default {
  data() {
    return {};
  },
  mounted() {
    console.log(this.$route.query); // 파라미터 확인용
    // 파라미터를 이용한 로직 수행
  },
  methods: {
    PaymentRequest() {
    const { mnum, memail, mname, onum, dpprice, mtel } = this.$route.query;
    const randomNumber = Math.floor(Math.random() * 10000);

    // 결제 요청 데이터 설정
    const data = {
        pg: 'html5_inicis.INIpayTest', // 이니시스 결제창 사용
        pay_method: 'card', // 결제 수단: 카드
        merchant_uid: `${onum}${mnum}_${randomNumber}`, // 상점 거래 ID
        amount: dpprice, // 결제 금액
        name: '더치오더', // 상품명 설정
        buyer_email: memail, // 구매자 이메일
        buyer_name: mname, // 구매자 이름
        buyer_tel: mtel, // 구매자 전화번호
        // m_redirect_url: 'http://yourdomain.com/payment/callback', // 결제 완료 후 이동할 URL
    };

    // 아임포트 결제창 오픈
    const IMP = window.IMP;
    IMP.init('imp38512704'); // 아임포트에서 발급받은 식별코드 사용
    IMP.request_pay(data, function(response) {
        // 결제창이 닫힌 후 처리할 로직
        if (response.success) {
            // 결제 성공 시
            axios.post('http://localhost:8888/api/payResult', {onum: onum, mname: mname});
            alert('결제가 완료되었습니다.');
            // 이전 페이지의 URL 저장
            var previousUrl = document.referrer;
            // 결제가 완료된 후 이전 페이지로 리디렉션
            window.location.href = previousUrl;
        } else {
            // 결제 실패 시
            const errMsg = '결제에 실패하였습니다: ' + response.error_msg;
            alert(errMsg);
            console.error(errMsg);
        }
    });
    }
  }
};
</script>
