
import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import PayRequest from '../views/client/C_payrequest.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/C_chatbot',
    name: 'C_chatbot',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import('../components/client/C_chatbot.vue')
  },
  {
    path: '/C_membersave',
    name: 'C_membersave',
    component: () => import('../views/client/C_membersave.vue')
  },
  {
    path: '/C_OrdPlaced',
    name: 'C_OrdPlaced',
    component: () => import('../views/client/C_OrdPlaced.vue')
  },
  {
    path: '/C_CLcard',
    name: 'C_CLcard',
    component: () => import('../components/client/C_CLcard.vue')
  },
  {
    path: '/C_CLcardOpt',
    name: 'C_CLcardOpt',
    component: () => import('../components/client/C_CLcardOpt.vue')
  },
  {
    path: '/C_payrequest',
    name: 'C_payrequest',
    component: PayRequest
  },
  {
    path: '/C_MainCategory',
    name: 'C_MainCategory',
    component: () => import('../views/client/C_MainCategory.vue')
  },
  {
    path: '/C_Ordering',
    name: 'C_Ordering',
    component: () => import('../views/client/C_Ordering.vue')
  },
  {
    path: '/B_shopUpload',
    name: 'B_shopUpload',
    component: () => import('../views/business/B_shopUpload.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})



export default router
